import { Badge } from '@material-ui/core';
import { Search, ShoppingCartOutlined } from '@material-ui/icons';
import React from 'react';
import styled from 'styled-components';
import { mobile } from '../responsive';
import { NavLink } from 'react-router-dom';

const Container = styled.div`
  height: 60px;
  ${mobile({ height: '50px' })}
`;

const Wrapper = styled.div`
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  ${mobile({ padding: '10px 0px' })}
`;

const Left = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
`;

const Language = styled.span`
  font-size: 14px;
  cursor: pointer;
  ${mobile({ display: 'none' })}
`;

const SearchContainer = styled.div`
  border: 0.5px solid lightgray;
  display: flex;
  align-items: center;
  margin-left: 25px;
  padding: 5px;
`;

const Input = styled.input`
  border: none;
  ${mobile({ width: '50px' })}
  &:focus {
    outline: none;
  }
`;

const Center = styled.div`
  flex: 1;
  text-align: center;
`;

const Logo = styled.h1`
  font-weight: bold;
  cursor: pointer;
  ${mobile({ fontSize: '24px' })}
`;
const Right = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  ${mobile({ flex: 2, justifyContent: 'center' })}
`;

const MenuItem = styled.div`
  font-size: 14px;
  cursor: pointer;
  margin-left: 25px;
  ${mobile({ fontSize: '12px', marginLeft: '10px' })}
`;

const Navbar = () => {
  return (
    <Container>
      <Wrapper>
        <Left>
          <Language>EN</Language>
          <SearchContainer>
            <Input placeholder="Search" />
            <Search
              style={{ color: 'gray', fontSize: 16, cursor: 'pointer' }}
            />
          </SearchContainer>
        </Left>
        <Center>
          <NavLink
            to={{ pathname: '/' }}
            style={({ isActive }) => ({
              textDecoration: 'none',
              color: 'black',
            })}
          >
            <Logo>TIDA</Logo>
          </NavLink>
        </Center>
        <Right>
          <MenuItem>
            <NavLink
              to={{ pathname: '/register' }}
              style={({ isActive }) => ({
                textDecoration: 'none',
                color: 'black',
              })}
            >
              REGISTER
            </NavLink>
          </MenuItem>
          <MenuItem>
            <NavLink
              to={{ pathname: '/login' }}
              style={({ isActive }) => ({
                textDecoration: 'none',
                color: 'black',
              })}
            >
              SIGN IN
            </NavLink>
          </MenuItem>
          <MenuItem>
            <Badge badgeContent={2} color="primary">
              <NavLink
                to={{ pathname: '/cart' }}
                style={({ isActive }) => ({
                  textDecoration: 'none',
                  color: 'black',
                })}
              >
                <ShoppingCartOutlined />
              </NavLink>
            </Badge>
          </MenuItem>
        </Right>
      </Wrapper>
    </Container>
  );
};

export default Navbar;
